import React, { Component } from "react";
import Topbar from "../../components/Topbar/index";
import ReturnTop from "../../components/ReturnTop/index";
import Footer from "../../components/Footer/index";
import SideBar from "../../components/SideBar/index";
import { connect } from "react-redux";
import { Row, Col, Table, Button, Dropdown } from "react-bootstrap";
import DatePicker from "react-datepicker";
import {
  countryFreight,
  country,
  countryList
} from "../../utils/countryFreight";
class International extends Component {
  constructor(props) {
    super(props);
    this.state = {
      islogin: false,

      width: "",
      weight: "",
      countryId: "",
      long: "",
      height: "",
      freight: []
    };
  }
  componentDidMount() {
    this.checkIsLogin();
  }

  checkIsLogin() {
    let user = sessionStorage.getItem("user");
    if (user) {
      this.setState({
        islogin: true
      });
    }
  }
  changePlat(a) {
    this.props.dispatch({ type: "PLATCHANGE", plat: a });
  }
  getData() {}
  showLogin() {}
  getFreight() {
    let { height, weight, long, width, countryId } = this.state;
    height = height ? height : 0;
    weight = weight ? weight : 1000;
    long = long ? long : 0;
    width = width ? width : 0;
    let ary = [];
    let gt90 = (w, h, l, kg) => {
      let num = ((h * l * w) / 6000) * kg * (0.95 + 0.2) + 10;
      return num.toFixed(2);
    };
    let lt90 = (w, kg, pc) => {
      let num = ((kg / 1000) * w + pc) * (0.95 + 0.2) + 10;
      return num.toFixed(2);
    };

    countryList.map((a, b) => {
      let obj = {}; // dhlpps dhlzx sfsx sfzx sfxb
      obj.country = country[a];
      if (countryId && country[a] !== countryId) {
        return null;
      }
      if (countryFreight[a]) {
        if (height + long + width > 90) {
          // countryFreight[a]
          if (countryFreight[a].dhl.pps.weightLimit) {
            let dhlppskglt = countryFreight[a].dhl.pps.lt.kg;
            let dhlppskggt = countryFreight[a].dhl.pps.gt.kg;
            let dhlppslt = gt90(width, height, long, dhlppskglt);
            let dhlppsgt = gt90(width, height, long, dhlppskggt);

            obj.dhlppslt = dhlppslt;
            obj.dhlppsgt = dhlppsgt;
            obj.dhlppslimit = countryFreight[a].dhl.pps.weightLimit;
          } else {
            let dhlppskg = countryFreight[a].dhl.pps.kg;
            let dhlpps = gt90(width, height, long, dhlppskg);

            obj.dhlpps = dhlpps;
          }
          obj.dhlppstime = countryFreight[a].dhl.pps.durTime;
          if (countryFreight[a].dhl.zx) {
            if (countryFreight[a].dhl.zx.weightLimit) {
              let dhlzxkglt = countryFreight[a].dhl.zx.lt.kg;
              let dhlzxkggt = countryFreight[a].dhl.zx.gt.kg;
              let dhlzxlt = gt90(width, height, long, dhlzxkglt);
              let dhlzxgt = gt90(width, height, long, dhlzxkggt);

              obj.dhlzxlt = dhlzxlt;
              obj.dhlzxgt = dhlzxgt;
              obj.dhlzxlimit = countryFreight[a].dhl.zx.weightLimit;
            } else {
              let dhlzxkg = countryFreight[a].dhl.pps.kg;
              let dhlzx = gt90(width, height, long, dhlzxkg);

              obj.dhlzx = dhlzx;
            }
            obj.dhlzxtime = countryFreight[a].dhl.zx.durTime;
          }
        } else {
          if (countryFreight[a].dhl.pps.weightLimit) {
            let dhlppskglt = countryFreight[a].dhl.pps.lt.kg;
            let dhlppspclt = countryFreight[a].dhl.pps.lt.pc;
            let dhlppspcgt = countryFreight[a].dhl.pps.gt.pc;
            let dhlppskggt = countryFreight[a].dhl.pps.gt.kg;
            let dhlppslt = lt90(weight, dhlppskglt, dhlppspclt);
            let dhlppsgt = lt90(weight, dhlppskggt, dhlppspcgt);

            obj.dhlppslt = dhlppslt;
            obj.dhlppsgt = dhlppsgt;
            obj.dhlppslimit = countryFreight[a].dhl.pps.weightLimit;
          } else {
            let dhlppskg = countryFreight[a].dhl.pps.kg;
            let dhlppspc = countryFreight[a].dhl.pps.pc;
            let dhlpps = lt90(weight, dhlppskg, dhlppspc);

            obj.dhlpps = dhlpps;
          }
          obj.dhlppstime = countryFreight[a].dhl.pps.durTime;

          if (countryFreight[a].dhl.zx) {
            if (countryFreight[a].dhl.zx.weightLimit) {
              let dhlzxkglt = countryFreight[a].dhl.zx.lt.kg;
              let dhlzxpclt = countryFreight[a].dhl.zx.lt.pc;
              let dhlzxkggt = countryFreight[a].dhl.zx.gt.kg;
              let dhlzxpcgt = countryFreight[a].dhl.zx.gt.pc;

              let dhlzxlt = lt90(weight, dhlzxkglt, dhlzxpclt);
              let dhlzxgt = lt90(weight, dhlzxkggt, dhlzxpcgt);

              obj.dhlzxlt = dhlzxlt;
              obj.dhlzxgt = dhlzxgt;
              obj.dhlzxlimit = countryFreight[a].dhl.zx.weightLimit;
            } else {
              let dhlzxkg = countryFreight[a].dhl.zx.kg;
              let dhlzxpc = countryFreight[a].dhl.zx.pc;
              let dhlzx = lt90(weight, dhlzxkg, dhlzxpc);

              obj.dhlzx = dhlzx;
            }
            obj.dhlzxtime = countryFreight[a].dhl.zx.durTime;
          }
        }
      }

      ary.push(obj);
    });
    this.setState({
      freight: ary
    });
  }
  render() {
    const {
      islogin,
      weight,
      countryId,
      long,
      width,
      height,
      freight
    } = this.state;
    // console.log(this.props.state);

    return (
      <div className="home orders">
        {islogin ? (
          <div className="home_left">
            <SideBar {...this.props} />
          </div>
        ) : null}

        <div
          className="home_right"
          style={{ paddingLeft: islogin ? "118px" : 0 }}
        >
          <Topbar
            {...this.props}
            islogin={this.state.islogin}
            changePlat={a => {
              this.changePlat(a);
            }}
          />
          <ReturnTop />

          <div className="main">
            <div className="header">
              <span></span>
            </div>
            <div className="search">
              <ul
                className="flexul"
                style={{ display: "flex", justifyContent: "space-between" }}
              >
                <li className="item">
                  <div className="title"></div>
                  <input
                    type="number"
                    value={long}
                    onChange={e => {
                      this.setState({
                        long: e.target.value
                      });
                    }}
                  />
                  <span style={{ marginLeft: "8px" }}>cm</span>
                </li>
                <li className="item">
                  <div className="title"></div>
                  <input
                    type="number"
                    value={width}
                    onChange={e => {
                      this.setState({
                        width: e.target.value
                      });
                    }}
                  />
                  <span style={{ marginLeft: "8px" }}>cm</span>
                </li>
                <li className="item">
                  <div className="title"></div>
                  <input
                    type="number"
                    value={height}
                    onChange={e => {
                      this.setState({
                        height: e.target.value
                      });
                    }}
                  />
                  <span style={{ marginLeft: "8px" }}>cm</span>
                </li>
              </ul>
              <ul
                className="flexul"
                style={{ display: "flex", justifyContent: "space-between" }}
              >
                <li className="item">
                  <div className="title"></div>
                  <Dropdown>
                    <Dropdown.Toggle variant="success" id="dropdown-basic">
                      {countryId === "" ? "" : countryId}
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      {countryList.map((a, b) => {
                        return (
                          <Dropdown.Item
                            onClick={() => {
                              this.setState({
                                countryId: country[a]
                              });
                            }}
                          >
                            {country[a]}
                          </Dropdown.Item>
                        );
                      })}
                    </Dropdown.Menu>
                  </Dropdown>
                </li>
                <li className="item">
                  <div className="title"></div>
                  <input
                    type="number"
                    value={weight}
                    onChange={e => {
                      this.setState({
                        weight: e.target.value
                      });
                    }}
                  />
                  <span style={{ marginLeft: "8px" }}>g</span>
                </li>
              </ul>
              <div className="btns" style={{ marginBottom: "15px" }}>
                <Button
                  onClick={() => {
                    this.getFreight();
                  }}
                >
                  
                </Button>
              </div>
            </div>
            <div className="table">
              <Table striped bordered hover>
                <thead>
                  <tr>
                    <th></th>
                    <th>DHL1</th>
                    <th>DHL2</th>
                    <th>1</th>
                    <th>2</th>
                  </tr>
                </thead>
                <tbody>
                  {freight.map((a, b) => {
                    return (
                      <tr>
                        <td>{a.country}</td>
                        <td>
                          {a.dhlppslimit
                            ? `<=${a.dhlppslimit}g ${a.dhlppslt};>${
                                a.dhlppslimit
                              }g ${a.dhlppsgt};`
                            : a.dhlpps
                            ? `${a.dhlpps}`
                            : "-"}
                          {a.dhlppstime ? `(${a.dhlppstime})` : null}
                        </td>
                        <td>
                          {a.dhlzxlimit
                            ? `<=${a.dhlzxlimit}g ${a.dhlzxlt};>${
                                a.dhlzxlimit
                              }g ${a.dhlzxgt};`
                            : a.dhlzx
                            ? `${a.dhlzx}`
                            : "-"}
                          {a.dhlzxtime ? `(${a.dhlzxtime})` : null}
                        </td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </div>
          </div>
          <Footer />
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    state
  };
};

const mapDispatchToProps = dispatch => {
  return {
    dispatch
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(International);
